#dashboard {
    align-items: center;
    display: flex;
    flex-direction: column;
    flex-grow: 1;
}

#dashboard-container {
    display: flex;
    flex-direction: row-reverse;
    max-width: 768px;
    width: 100%;
}

#scene-container {
    min-width: 240px;
    width: 240px;
}

#device-container {
    flex-grow: 1;
}

#dashboard .panel-header {
    display: flex;
    margin: 10px 10px 0 10px;
}

#dashboard .panel-header h2 {
    flex-grow: 1;
    font-size: 1em;
    font-weight: normal;
    margin: 0;
    padding: 0;
}

#device-favorites {
    display: flex;
    flex-wrap: wrap;
    gap: 16px;
}

/* TODO - device-favorites pushes scene-favorites out of the viewable area on very small screens! */

@media screen and (max-width: 800px) {
    #dashboard-container {
        flex-wrap: wrap;
        overflow-y: auto;
    }

    #device-container, #scene-container {
        width: 100%;
    }

    #device-favorites {
        gap: 10px;
    }
}